<template>
  <div class="my-todos">
    <div class="container my-5 text-capitalize">
      <div class="todos-table">
        <table
          class="mx-auto shadow p-4 rounded-4 table"
          v-if="todoList.length"
        >
          <thead>
            <tr>
              <th>Text</th>
              <th>From</th>
              <th>To</th>
              <th>Created At</th>
              <th>completed</th>
              <th>deleted</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(todo, index) in todoList"
              :key="todo.id"
              :style="`${
                todo.isCompleted
                  ? '--bs-table-bg:green ; --bs-body-color:white'
                  : '--bs-table-bg:transparent'
              }`"
            >
              <td>{{ todo.text }}</td>
              <td>{{ todo.from }}</td>
              <td>{{ todo.to }}</td>
              <td>{{ new Date(todo.createdAt).toLocaleDateString() }}</td>
              <td>
                <button
                  class="btn btn-warning"
                  @click="MarkcompletedTodo(todo)"
                >
                  {{ todo.isCompleted ? "Incompleted" : "Completed" }}
                </button>
              </td>
              <td>
                <button class="btn btn-danger" @click="deleteTodo(index)">
                  deleted
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <h2 v-else class="lh-lg text-capitalize text-bg-danger">
          No todos to show
        </h2>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
const todoList = ref([]);

// ----------------------------------check local storage
const checkLocal = () => {
  if (localStorage.getItem("todos")) {
    todoList.value = JSON.parse(localStorage.getItem("todos"));
    console.log(todoList.value);
  }
};
// ----------------------------------- delete method
const deleteTodo = (index) => {
  todoList.value.splice(index, 1);
  addToLocalStorage();
};
// ----------------------------------- mark as completed method
const MarkcompletedTodo = (todo) => {
  todo.isCompleted = !todo.isCompleted;
  addToLocalStorage();
};

// ---------------------------------- add or set to local storage -------------------
const addToLocalStorage = () => {
  localStorage.setItem("todos", JSON.stringify(todoList.value));
};

// ---------- on mounted method
onMounted(() => {
  checkLocal();
});
</script>

<style scoped lang="scss"></style>
